	module.exports = {"data":{"site":{"siteMetadata":{"title":"Logrinto Blog","menu":[{"label":"Home","path":"/"},{"label":"Über uns","path":"/ueber-uns/"},{"label":"Kontakt","path":"/kontakt/"}],"subtitle":"Blog zum Interaction Designer an der Schule für Gestaltung Zürich","copyright":"CC-BY-SA 4.0","author":{"name":"Logrinto","email":"info@logrinto.ch","instagram":"https://www.instagram.com/logrinto/","twitter":"https://twitter.com/logrinto","github":"https://github.com/logrinto/"}}},"allMarkdownRemark":{"edges":[{"node":{"fields":{"slug":"/posts/tests/","categorySlug":"/categories/responsive-gestaltung/"},"frontmatter":{"title":"Test","date":"2022-12-31","category":"Responsive Gestaltung","description":"TLDR – Weit hinten, hinter den Wortbergen, fern der Länder Vokalien und Konsonantien leben die Blindtexte. Abgeschieden wohnen sie in Buchstabhausen an der Küste des Semantik, eines großen Sprachozeans. Ein kleines Bächlein namens Duden fließt durch ihren Ort und versorgt sie mit den nötigen Regelialien.","featuredImage":{"childImageSharp":{"sizes":{"base64":"data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAAHCAIAAACHqfpvAAAACXBIWXMAAAsTAAALEwEAmpwYAAAAEUlEQVQY02O4RgFgGNVMR80A4UtfKIGkbsEAAAAASUVORK5CYII=","aspectRatio":3,"src":"/static/header-745f8df0593536ace2c264afa5fc1af5-2fd78.png","srcSet":"/static/header-745f8df0593536ace2c264afa5fc1af5-739b5.png 200w,\n/static/header-745f8df0593536ace2c264afa5fc1af5-549e8.png 400w,\n/static/header-745f8df0593536ace2c264afa5fc1af5-2fd78.png 800w,\n/static/header-745f8df0593536ace2c264afa5fc1af5-c2444.png 1200w,\n/static/header-745f8df0593536ace2c264afa5fc1af5-af555.png 1600w,\n/static/header-745f8df0593536ace2c264afa5fc1af5-e0223.png 2400w,\n/static/header-745f8df0593536ace2c264afa5fc1af5-f59e7.png 3000w","sizes":"(max-width: 800px) 100vw, 800px"}}}}}},{"node":{"fields":{"slug":"/posts/tools/","categorySlug":"/categories/responsive-gestaltung/"},"frontmatter":{"title":"Do tools really Matter?","date":"2019-07-12","category":"Responsive Gestaltung","description":"In Designprozessen könnte vieles besser laufen. Gerade im Interaction Design – also wenn es darum geht Produkte oder Prozessschritte erlebbar zu machen, stossen die Design Tools öfters an ihre Grenzen oder die definierten Interaktionen können in der Entwicklung nicht so umgesetzt werden. Wie könnte man diesen Gap aufbrechen und gibt es «das Eine» Designtool?","featuredImage":{"childImageSharp":{"sizes":{"base64":"data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAAHCAIAAACHqfpvAAAACXBIWXMAAAsTAAALEwEAmpwYAAAAhUlEQVQY05WPPQpDIRCEPbcH8EJWVjb+NIInUBAtFCsbg4W+LEn1goZkuhn4dmbRddDjpbXWdRbapqWUEEKtNcb4hUfbTiCB11qnlMD+Ac85nXOEEIwxpbT3DuG2fz9bKcUYE0IALKU8ld/g93nvPec85wwWao0x1tpfZ7fWxhgfj2xnPwEoPZQO6CDlAQAAAABJRU5ErkJggg==","aspectRatio":3.0061349693251533,"src":"/static/header-c5be436e5a54b5289aac75df7b60997a-2fd78.png","srcSet":"/static/header-c5be436e5a54b5289aac75df7b60997a-739b5.png 200w,\n/static/header-c5be436e5a54b5289aac75df7b60997a-549e8.png 400w,\n/static/header-c5be436e5a54b5289aac75df7b60997a-2fd78.png 800w,\n/static/header-c5be436e5a54b5289aac75df7b60997a-d3a10.png 980w","sizes":"(max-width: 800px) 100vw, 800px"}}}}}},{"node":{"fields":{"slug":"/posts/typoartboard/","categorySlug":"/categories/responsive-gestaltung/"},"frontmatter":{"title":"Das perfekte Typo Artboard","date":"2019-07-12","category":"Responsive Gestaltung","description":"Für Designer ist oft nicht klar, wie man dem Entwickler am Besten vermittelt was es alles für Schrifthierarchien gibt. Zudem muss ihm der Designer eine Struktxur der Schrifteigenschaften mitgeben und wie sich diese verhaltet.","featuredImage":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":1.5,"src":"/static/header-image-a7e30fd77997e948e9ffd43dcdca34dc-2fd78.png","srcSet":"/static/header-image-a7e30fd77997e948e9ffd43dcdca34dc-739b5.png 200w,\n/static/header-image-a7e30fd77997e948e9ffd43dcdca34dc-549e8.png 400w,\n/static/header-image-a7e30fd77997e948e9ffd43dcdca34dc-2fd78.png 800w,\n/static/header-image-a7e30fd77997e948e9ffd43dcdca34dc-c2444.png 1200w,\n/static/header-image-a7e30fd77997e948e9ffd43dcdca34dc-af555.png 1600w,\n/static/header-image-a7e30fd77997e948e9ffd43dcdca34dc-e0223.png 2400w,\n/static/header-image-a7e30fd77997e948e9ffd43dcdca34dc-ecf02.png 6000w","sizes":"(max-width: 800px) 100vw, 800px"}}}}}},{"node":{"fields":{"slug":"/posts/em/","categorySlug":"/categories/responsive-gestaltung/"},"frontmatter":{"title":"Dynamische Masseinheit em","date":"2019-07-12","category":"Responsive Gestaltung","description":"Eine der besten Praktiken für die Typografie im Web ist die Verwendung relativer Einheiten wie em und rem. Relative Grössen werden in Abhängigkeit von anderen Masseinheiten angegeben und passen sich dadurch besser verschiedenen Ausgabemedien und Benutzereinstellungen an. Aber wann ist es sinnvoll em anzuwenden, wann besser rem?","featuredImage":{"childImageSharp":{"sizes":{"base64":"data:image/png;base64,","aspectRatio":2.6666666666666665,"src":"/static/header-8150d5176f9c19af81b8721207e7e11b-2fd78.png","srcSet":"/static/header-8150d5176f9c19af81b8721207e7e11b-739b5.png 200w,\n/static/header-8150d5176f9c19af81b8721207e7e11b-549e8.png 400w,\n/static/header-8150d5176f9c19af81b8721207e7e11b-2fd78.png 800w,\n/static/header-8150d5176f9c19af81b8721207e7e11b-c2444.png 1200w,\n/static/header-8150d5176f9c19af81b8721207e7e11b-af555.png 1600w","sizes":"(max-width: 800px) 100vw, 800px"}}}}}},{"node":{"fields":{"slug":"/posts/zusammenfassung/","categorySlug":"/categories/responsive-gestaltung/"},"frontmatter":{"title":"Die Sezierung responsiver Web-Gestaltung","date":"2019-07-12","category":"Responsive Gestaltung","description":null,"featuredImage":{"childImageSharp":{"sizes":{"base64":"data:image/png;base64,","aspectRatio":3,"src":"/static/header-b4df787bae39275655d0d21cc7984aa9-2fd78.png","srcSet":"/static/header-b4df787bae39275655d0d21cc7984aa9-739b5.png 200w,\n/static/header-b4df787bae39275655d0d21cc7984aa9-549e8.png 400w,\n/static/header-b4df787bae39275655d0d21cc7984aa9-2fd78.png 800w,\n/static/header-b4df787bae39275655d0d21cc7984aa9-c2444.png 1200w,\n/static/header-b4df787bae39275655d0d21cc7984aa9-af555.png 1600w,\n/static/header-b4df787bae39275655d0d21cc7984aa9-e0223.png 2400w,\n/static/header-b4df787bae39275655d0d21cc7984aa9-f59e7.png 3000w","sizes":"(max-width: 800px) 100vw, 800px"}}}}}},{"node":{"fields":{"slug":"/posts/viewport-einheiten/","categorySlug":"/categories/responsive-gestaltung/"},"frontmatter":{"title":"Wann lohnt sich der Einsatz von Viewport Einheiten?","date":"2019-07-12","category":"Responsive Gestaltung","description":"Der Viewport ist der Bereich wo der Browser die Seite rendert. Manchmal möchte man Elemente wie eine Sidebar oder ein Grid am Viewport ausrichten. Dies kann mit den neuen Viewport Units erreicht werden. Aber wo genau liegen Potenzial und Einschränkungen dieser Einheiten?","featuredImage":{"childImageSharp":{"sizes":{"base64":"data:image/png;base64,","aspectRatio":2.9954954954954953,"src":"/static/header-b3de8e63a4039b423ecb0173fd83eee2-2fd78.png","srcSet":"/static/header-b3de8e63a4039b423ecb0173fd83eee2-739b5.png 200w,\n/static/header-b3de8e63a4039b423ecb0173fd83eee2-549e8.png 400w,\n/static/header-b3de8e63a4039b423ecb0173fd83eee2-2fd78.png 800w,\n/static/header-b3de8e63a4039b423ecb0173fd83eee2-c2444.png 1200w,\n/static/header-b3de8e63a4039b423ecb0173fd83eee2-af555.png 1600w,\n/static/header-b3de8e63a4039b423ecb0173fd83eee2-e0223.png 2400w,\n/static/header-b3de8e63a4039b423ecb0173fd83eee2-e6188.png 3325w","sizes":"(max-width: 800px) 100vw, 800px"}}}}}},{"node":{"fields":{"slug":"/posts/bezugselemente/","categorySlug":"/categories/responsive-gestaltung/"},"frontmatter":{"title":"Mit Prozent zum flüssigen Design","date":"2019-07-12","category":"Responsive Gestaltung","description":"Im Responsive Design wird nur noch selten mit Pixelwerten gearbeitet. Pixelwerte sind zu starr, um auf veränderte Device- und Browser-Grössen zu reagieren. Elementweiten in Prozent sind deshalb unverzichtbar, allerdings muss der Designer auch Kontrolle aus der Hand geben. Wir beleuchten :bulb: den Umgang mit Prozentwerten, die Vorteile und die Nachteile, im Web in diesem Beitrag.","featuredImage":{"childImageSharp":{"sizes":{"base64":"data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAAHCAIAAACHqfpvAAAACXBIWXMAAAsTAAALEwEAmpwYAAAAEUlEQVQY02O4RgFgGNVMR80A4UtfKIGkbsEAAAAASUVORK5CYII=","aspectRatio":3,"src":"/static/header-745f8df0593536ace2c264afa5fc1af5-2fd78.png","srcSet":"/static/header-745f8df0593536ace2c264afa5fc1af5-739b5.png 200w,\n/static/header-745f8df0593536ace2c264afa5fc1af5-549e8.png 400w,\n/static/header-745f8df0593536ace2c264afa5fc1af5-2fd78.png 800w,\n/static/header-745f8df0593536ace2c264afa5fc1af5-c2444.png 1200w,\n/static/header-745f8df0593536ace2c264afa5fc1af5-af555.png 1600w,\n/static/header-745f8df0593536ace2c264afa5fc1af5-e0223.png 2400w,\n/static/header-745f8df0593536ace2c264afa5fc1af5-f59e7.png 3000w","sizes":"(max-width: 800px) 100vw, 800px"}}}}}},{"node":{"fields":{"slug":"/posts/codedesign/","categorySlug":"/categories/responsive-gestaltung/"},"frontmatter":{"title":"Code und Design","date":"2019-06-12","category":"Responsive Gestaltung","description":"UX/UI-Designer und Entwickler kommen oft an den Punkt, an dem jeder am liebsten alles selber in die Hand nehmen würde. Zu grosse Abweichungen in der Umsetzung des Designs, utopische Vorstellungen im Design an die Entwicklung und fehlende Angaben bei gewissen Breakpoints, sind nur ein Bruchteil der Probleme. Müssen Designer in Zukunft mehr ins Frontend und Entwickler sich mehr für Design interessieren? Und hat dieses Problem Einfluss auf die Tools der Zukunft? ","featuredImage":{"childImageSharp":{"sizes":{"base64":"data:image/png;base64,","aspectRatio":3,"src":"/static/header-6666f7de018ba4c0829c524bc4abaabd-2fd78.png","srcSet":"/static/header-6666f7de018ba4c0829c524bc4abaabd-739b5.png 200w,\n/static/header-6666f7de018ba4c0829c524bc4abaabd-549e8.png 400w,\n/static/header-6666f7de018ba4c0829c524bc4abaabd-2fd78.png 800w,\n/static/header-6666f7de018ba4c0829c524bc4abaabd-c2444.png 1200w,\n/static/header-6666f7de018ba4c0829c524bc4abaabd-af555.png 1600w,\n/static/header-6666f7de018ba4c0829c524bc4abaabd-e0223.png 2400w,\n/static/header-6666f7de018ba4c0829c524bc4abaabd-f59e7.png 3000w","sizes":"(max-width: 800px) 100vw, 800px"}}}}}},{"node":{"fields":{"slug":"/posts/dynamische-typografie/","categorySlug":"/categories/responsive-gestaltung/"},"frontmatter":{"title":"Alles bleibt anders – dynamische Typografie in digitalen Anwendungen","date":"2019-06-12","category":"Responsive Gestaltung","description":"Gerade den Silbentrennungen wird beim Gestalten einer Website häufig keine Aufmerksamkeit geschenkt, da davon ausgegangen wird, dass diese im Web nicht gross definiert werden können. Nachfolgend sollen darum einige Möglichkeiten aufgezeigt werden, wie auch bei digitalen Anwendungen auf die Typografie Einfluss genommen werden kann.","featuredImage":{"childImageSharp":{"sizes":{"base64":"data:image/png;base64,","aspectRatio":3,"src":"/static/header-img-135808eaccfc1094bafe8660fe5b7dcc-2fd78.png","srcSet":"/static/header-img-135808eaccfc1094bafe8660fe5b7dcc-739b5.png 200w,\n/static/header-img-135808eaccfc1094bafe8660fe5b7dcc-549e8.png 400w,\n/static/header-img-135808eaccfc1094bafe8660fe5b7dcc-2fd78.png 800w,\n/static/header-img-135808eaccfc1094bafe8660fe5b7dcc-c2444.png 1200w,\n/static/header-img-135808eaccfc1094bafe8660fe5b7dcc-af555.png 1600w,\n/static/header-img-135808eaccfc1094bafe8660fe5b7dcc-e0223.png 2400w,\n/static/header-img-135808eaccfc1094bafe8660fe5b7dcc-f59e7.png 3000w","sizes":"(max-width: 800px) 100vw, 800px"}}}}}}]}},"pathContext":{"category":"Responsive Gestaltung"}}

